.container {
  margin-left: auto;
  margin-right: auto;
  padding-right: 20px;
  padding-left: 20px;
  max-width: 375px;

  @media screen and (min-width: 768px) {
    padding-right: 32px;
    padding-left: 32px;
    max-width: 768px;
  }
  @media screen and (min-width: 1280px) {
    padding-right: 24px;
    padding-left: 24px;
    max-width: 1440px;
  }
}
